import { Prompt } from 'react-router-dom'

import { useState } from "react";
import { RootStateOrAny, useSelector } from "react-redux";
import Button from "../../components/UI/Button";
import Loader from "../../components/UI/Loader";

import '../../styles/game/pages/game-over.css';

// import Colours from "../../constants/Colours";
interface GameOverPageProps {
  isUpLoading: boolean,
  minutesLeft?: number,
  saveStadiumResult: Function,
  secondsLeft?: number,
  stadiumCounter: number,
  totalPoints: number,
  timer: boolean,
}
const GameOverPage = ({
  isUpLoading,
  minutesLeft,
  saveStadiumResult,
  secondsLeft,
  stadiumCounter,
  totalPoints,
  timer,
}: GameOverPageProps) => {
  const [hover, setHover] = useState(false);
  const [gradeSaved, setGradeSaved] = useState(false);

  const userIsLogedIn = useSelector((state: RootStateOrAny) => state.auth.userId);

  const handleHover = () => {
    setHover(!hover)
  }

  return (
    <>
      <Prompt
        when={!gradeSaved}
        message='         ,          "!" '
      />
      <section className='game-over' >
        <h2 className='epilogue' >    !</h2>
        <h3 className='stadium' >: {stadiumCounter}</h3>
        <h4 className='grade' > : {totalPoints} </h4>
        {timer && (
          <>
            <div onClick={() => {
              alert(`        
(  ) 
   ( )  10.`)
            }}>

              {/* <MaterialIcons
                  name="info-outline"
                  size={Math.ceil(width * 0.08)}
                  color={Colours.maroon}
                /> */}
            </div>
            <h5>
                : {minutesLeft}': {secondsLeft}''
            </h5>
          </>
        )}
        {userIsLogedIn ? (
          <div className='instruction-text' >
            <h5>
               
            </h5>
            <h5>
                  
            </h5>
          </div>
        ) : (
          <span>E   :</span>
        )}
        {isUpLoading ? (
          <Loader />
        ) : (
          <Button
            disabled={false}
            title=''
            onClick={() => {
              setGradeSaved(true);
              saveStadiumResult()
            }}
            onMouseEnter={() => handleHover()}
            onMouseLeave={() => handleHover()}
          />
        )}
      </section>
    </>
  );
};

export default GameOverPage;
